.app {
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  &__form {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 300px;

    &--item {
      margin: 10px;
    }
  }
}

.error {
  color: red;
}

.button {
  margin: 10px auto 0;
  width: 100px;
  height: 30px;
  background-color: rgba($color: #03742e6e, $alpha: 0.5);
  border: none;
  border-radius: 5px;
  box-shadow: 10px 5px 15px rgb(199, 199, 199);
}
